import React  from 'react'

import {Spinner, SetSpinnerTime} from '../../components/spinner/Spinner'
import MainCards from '../../components/MainCarts'
import Nav from '../../components/Nav'
import Footer from '../../components/Footer'

const Main = () => {
  return (
    <>
        <Nav />
        {
          SetSpinnerTime() ?
            <>
              <MainCards />
              <Footer />
            </>
          :
            <Spinner />
        }
    </>
  )
}

export default Main
