import React from 'react'
import { motion } from 'framer-motion'
import { Link } from 'react-router-dom'
import research_proj from '../assets/ResearchProj.png'
import implemen_proj from '../assets/implementProj2.jpg'
import books from '../assets/books.jpg'
import Articles from '../assets/Articles.png'
import movies from '../assets/movie_neon1.jpg'
import accept_proj from '../assets/acceptProj1.jpg'
// import about_me from '../assets/all-about-me-neon.jpg'

const list = [
    {id:1, name:'  ', link:'/ResearchProj', img:research_proj},
    {id:2, name:'  ', link:'/ImplementProj', img:implemen_proj},
    {id:3, name:'', link:'Articles', img:Articles},
    {id:4, name:'       ', link:'/AcceptProject', img:accept_proj},
    {id:5, name:' ', link:'Books', img:books},
    {id:6, name:' ', link:'Media', img:movies},
]

function Cards() {

    const variants = {
        initial:{
            opacity:0, 
            x:+100
        },
        visible: (custom) => ({
            opacity:1, 
            x:0,
            transition: { delay: custom/4 }
        }),
      }

      
    return(
        list.map(({img,name,link,id})=>(
            <motion.div 
                className='mainCardItems' 
                whileHover={{scale:1.02}}
                transition={{duration:.5}}
                key={id} 
                custom={id} 
                animate="visible" 
                variants={variants} 
                initial={'initial'}
            >
                <Link className='w_100 h_100 center boxColumn gap_1' style={{backgroundImage: `url(${img})`}} to={link}>
                    <h4 className='h4 txtWhite mt_2'>   
                        {name}
                    </h4>
                    {/* <p className='btn_transparent'>
                         
                    </p> */}
                </Link>
                
            </motion.div>
        ))
    )
}

const MainCarts = () => {
  return (
    <section className='mainCard'>
      <Cards />
    </section>
  )
}

export default MainCarts
