import React from 'react'
import { motion } from 'framer-motion'
import { Link } from 'react-router-dom'
import { BooksList } from '../data/List'

const animVariant = {
    initialLeft:{x:-100, opacity:0},
    initialRight:{x:+100, opacity:0},
    start: (custom) => ({
        x:0, 
        opacity:1,
        transition: { duration: .6, delay: .2}
    })
}

const ShowCards = () => {
    return(
        BooksList.map((item, key)=>(
            <motion.div 
                key={key}
                className='BPBox'
                variants={animVariant}
                initial='initialRight'
                whileInView='start'
                custom={key}
                viewport={{ once: true }}
            >
                <div className='BPImg' >
                    <motion.img  
                        src={item.img} 
                        alt={item.name} 
                        whileHover={{scale:1.1}}
                        className='imgGrayScale'
                    />
                </div>
                <div className='BPBody'>
                    <h3 className='h3 pb_2'>{item.name}</h3>
                    {/* <h3 className='h3'>{item.eName}</h3> */}
                    <p className='txtBody'>
                        <span className='txtWhite'> : </span>
                        <span>{item.publisher}</span>
                    </p>
                    <p className='txtBody'>
                        <span className='txtWhite'>  : </span>
                        <span>{item.pages}</span>
                    </p>
                    <p className='txtBody'>
                        <span className='txtWhite'>   : </span>
                        <span>{item.lastPublish}</span>
                    </p>
                    <p className='txtBody'>
                        <span className='txtWhite'> :</span>
                        <span>{item.price}</span>
                        <span>  </span>
                    </p>
                    <p className='center pt_2'>
                        <Link to={item.link + "/" + item.name} state={{ id: key }} className='btn_transparent'>
                              
                        </Link>
                    </p>
                </div>
            </motion.div>
        ))
    )
}

const BooksProducts = () => {
  return (
    <section className='gap_2 boxColumn'>
      <ShowCards />
    </section>
  )
}

export default BooksProducts
