import React from 'react'
import { motion } from 'framer-motion'

const Header = ({name}) => {
  return (
    <motion.header 
      initial={{opacity:0}} 
      animate={{opacity:1}} 
      transition={{duration: .6, delay: .7}}
      className='w_100 p_3'
    >
      <h1 className='h1 center textCenter'>
        {name}
      </h1>
    </motion.header>
  )
}

export default Header
