
import React,{ useState} from 'react';
import {Link} from 'react-router-dom'
import { motion, AnimatePresence } from 'framer-motion'
import { BsInstagram, BsWhatsapp, BsTelegram } from 'react-icons/bs';
import Eitaa_Logo from '../assets/icons/eitaa_black.png'
import Soroush_Logo from '../assets/icons/soroush_black.png'
import Bale_Logo from '../assets/icons/bale_black.png'

// import logo from '../assets/logo/logo.svg'
import drawerLogo from '../assets/drawer.svg'

const nav_list = [
  {id:1, name:" ", navigate:"/AboutMe"},
  {id:2, name:"  ", navigate:"/ContactUs"},
  {id:3, name:" ", navigate:"/acceptProject"},
  {id:4, name:" ", navigate:"/Books"},
  {id:5, name:"", navigate:"/"},
]

const NavLinks = ({animation}) => {
  return(
    animation ?
      nav_list.slice(0).reverse().map((item,key)=>(
        <motion.li 
          key={item.id}
          initial={{ x: -100, opacity: 0 }}
          animate={{ x: 0, opacity: 1 }}
          transition={{delay:key / 5}}
        >
          <Link animate={{scale:1.2}} to={item.navigate} state={{ pageName: item.name }} preventScrollReset={false} className='nav_a'>
            {item.name}
          </Link>
        </motion.li>
      ))
    :
    nav_list.map((item,key)=>(
      <motion.li key={item.id}>
        <Link to={item.navigate} state={{ pageName: item.name }} preventScrollReset={false} className='nav_a'>
          {item.name}
        </Link>
      </motion.li>
    ))
  )
}

const Nav = () => {
  const [mobileOpen, setMobileOpen] = useState(false);
  
  const handleDrawerToggle = () => {
      setMobileOpen(!mobileOpen);
  };

  const drawer = (
    <AnimatePresence>
      {
        mobileOpen && (
          <motion.div className='navDrawer boxRow'>
            <motion.div 
              key={'Drawer'}
              className='navDrawerLeft boxColumn top gap_2 pt_2'
              initial={{ x: -100 }}
              animate={{ x: 0 }}
              exit={{ width: -100, opacity:0 }}
            >
              <NavLinks animation={'run'}/>
            </motion.div>
            <motion.div className='navDrawerRight' onClick={handleDrawerToggle}/>
          </motion.div>
        )
      }
    </AnimatePresence>
  );

  return (
    <motion.nav 
      className='boxRow'
      initial={{y: -100}}
      animate={{y: 0}}
      transition={{duration: .6, delay: 0}}
    >
      {/* -------if drawer show------- */}
      <div className='nav_cardDrawer left p_1' onClick={handleDrawerToggle}>
        <img src={drawerLogo} alt={'Drawer'} />
      </div>

      <div className='nav_social gap_2'>
        <img src={Eitaa_Logo} alt='Eita' className='nav_logo_svg'/>
        <img src={Soroush_Logo} alt='Eita' className='nav_logo_svg'/>
        <img src={Bale_Logo} alt='Eita' className='nav_logo_svg'/>
      </div>
      <ul className='boxRow gap_1 nav_cardLink center'>
        <NavLinks />
      </ul>
      <div className='nav_logo right'>
        <h1 className='h3'>
            
        </h1>
      </div>      
      {/* ---- Show Drawer */}
      {
        drawer
      }
    </motion.nav>
  )
}

export default Nav
