function Javayez() {
    return(
        javayezList.map((item, key)=>(
            <motion.li 
                key={key}
                variants={animVariant}
                initial='initial'
                whileInView='start'
                custom={key/3}
                viewport={{ once: true }}
            >
                {item.name}
            </motion.li>
        ))
    )
}

