import React from 'react'
import { motion } from 'framer-motion'
import { Link } from 'react-router-dom'
import img1 from '../assets/a1.jpg'
import img2 from '../assets/a2.jpg'
import img3 from '../assets/a3.jpg'

let txt = '                           :   (  )                        .             .            .                   .       .           .                .                    .                     .              :   '

const list = [
    {id:1, name:' ',img:img1, link:'', desc:txt, },
    {id:2, name:' ',img:img2, link:'', desc:txt, },
    {id:3, name:' ',img:img3, link:'', desc:txt, },
]

const animVariant = {
    initialLeft:{x:-100, opacity:0},
    initialRight:{x:+100, opacity:0},
    start: {
        x:0, 
        opacity:1,
        transition: { duration: .6, delay: .2}
    }
}

const ShowCards = () => {
    return(
        list.map((item, key)=>(
            window.innerWidth >= 1024 ?
                key % 2 === 0 ?
                    <motion.div 
                        key={key}
                        className='PCBox'
                        variants={animVariant}
                        initial='initialRight'
                        whileInView='start'
                        viewport={{ once: true }}
                    >
                        <div className='PCBoxImg' >
                            <motion.img  
                                src={item.img} 
                                alt={item.name} 
                                whileHover={{scale:1.08}}
                            />
                        </div>
                        <div className='PCBoxBody'>
                            <h3 className='h3'>{item.name}</h3>
                            <p className='txtBody'>{item.desc}</p>
                        </div>
                    </motion.div>
                :
                    <motion.div 
                        key={key}
                        className='PCBox'
                        variants={animVariant}
                        initial='initialLeft'
                        whileInView='start'
                        viewport={{ once: true }}
                    >
                        <div className='PCBoxBody'>
                            <h3 className='h3'>{item.name}</h3>
                            <p className='txtBody'>{item.desc}</p>
                        </div>
                        <div className='PCBoxImg' >
                            <motion.img  
                                src={item.img} 
                                alt={item.name} 
                                whileHover={{scale:1.08}}
                            />
                        </div>
                    </motion.div>
                :
                    <motion.div 
                        key={key}
                        className='PCBox'
                        variants={animVariant}
                        initial='initialRight'
                        whileInView='start'
                        viewport={{ once: true }}
                    >
                        <div className='PCBoxImg' >
                            <motion.img  
                                src={item.img} 
                                alt={item.name} 
                                whileHover={{scale:1.08}}
                            />
                        </div>
                        <div className='PCBoxBody'>
                            <h3 className='h3'>{item.name}</h3>
                            <p className='txtBody'>{item.desc}</p>
                        </div>
                    </motion.div>
        ))
    )
}

const ProjCard = () => {
  return (
    <section className='gap_2 boxColumn'>
      <ShowCards />
      <Link to="/AcceptProject" className='btn_transparent' style={{marginTop:'-4rem'}} >  </Link>
    </section>
  )
}

export default ProjCard
