import React from 'react'
import { useFormik } from "formik";
import * as yup from 'yup';
import emailjs from 'emailjs-com';
import {motion} from 'framer-motion'

function MyTextInput({label, errorClass, children, ...props}) {
  // const [field, meta] = useField(props);
   return (
     <>
      <label className={'h4 txtWhite'} dir='rtl' id={props.width} htmlFor={props.name} >
        {label}
        <input 
          type="text" 
          {...props} 
          dir={props.direction}
          className={errorClass}
        />
        {children}
      </label>
     </>
   );
}

const ContactCard = ({LoadState}) => {

  const validationSchema = yup.object().shape({
    firstName: yup.string()
      .max(15, '    ')
      .min(3, '    ')
      .required('     '),
    lastName: yup.string()
      .max(15, '     ')
      .min(3, '     ')
      .required('      '),
    email: yup.string()
      .email(' ')
      .required('     '),
    message: yup.string()
      .min(10, '      ')
      .max(400, '     ')
      .required('     '),
  });

  const onSubmit = async (values, actions) => {
    LoadState(true)
      try {
        emailjs.send("service_xx7qu8d","template_jcv5rfc", {name: values.name, email: values.email ,message: values.message}, "OlMRJ4DsYqDs0xlPd")
          .then((result) => {
            alert('    ')
            LoadState(false)
            actions.resetForm()
          }, (error) => {
            console.log("error =>" + error)
            alert('   ')
            LoadState(false)
        });
      } catch (error) {
        LoadState(false)
          alert('   ')
      }
  };

  const {
    values,
    errors,
    touched,
    isSubmitting,
    handleBlur,
    handleChange,
    handleSubmit,
  } = useFormik({
    initialValues: {
      firstName: "",
      lastName: "",
      email: "",
      message: "",
    },
    validationSchema: validationSchema,
    onSubmit,
  });
  
  return(
    <motion.section 
      className='boxRow p_2 pr_3 center' 
      id='contact'
      initial={{opacity:0}} 
      animate={{opacity:1}} 
      transition={{duration: .6, delay: .6}}
    >
      <div className='w_25 boxColumn gap_2 ' dir='rtl'>
        <span dir='rtl'>
          <h3 className='h3'>:</h3>
          <p className='txtBody'>
             
          </p>
        </span>
        <span dir='rtl'>
          <h3 className='h3'>:</h3>
          <p className='txtBody txtGreen'>
            
          </p>
        </span>
        <span dir='rtl'>
          <h3 className='h3'>:</h3>
          <p className='txtBody txtGreen'>
            drkhalilnezhad@gmail.com
          </p>
        </span>
      </div>

      <div className='w_75'>
        <form id='form' onSubmit={handleSubmit} autoComplete="off">
          <MyTextInput
            label=" :"
            name="lastName"
            type="lastName"
            id="lastName"
            width="name"
            value={values.lastName}
            onChange={handleChange}
            onBlur={handleBlur}
            errorClass={errors.lastName && touched.lastName ? "input_error" : "input_sucsses"}
          >
            {errors.lastName && touched.lastName && <p className="txtError">{errors.lastName}</p>}
          </MyTextInput>

          <MyTextInput
            label=":"
            name="firstName"
            type="firstName"
            id="firstName"
            width="name"
            value={values.firstName}
            onChange={handleChange}
            onBlur={handleBlur}
            errorClass={errors.firstName && touched.firstName ? "input_error" : "input_sucsses"}
          >
            {errors.firstName && touched.firstName && <p className="txtError">{errors.firstName}</p>}
          </MyTextInput>

          <MyTextInput
            label=":"
            name="email"
            type="email"
            width="email"
            id="email"
            direction='ltr'
            placeholder="email@yahoo.com"
            value={values.email}
            onChange={handleChange}
            onBlur={handleBlur}
            errorClass={errors.email && touched.email ? "input_error" : "input_sucsses"}
          >
            {errors.email && touched.email && <p className="txtError">{errors.email}</p>}
          </MyTextInput>

          <label className='h4 txtWhite w_100' dir='rtl'>
             :
            <textarea 
              dir='rtl'
              className={ errors.message && touched.message ? "input_error" : "input_sucsses"}
              label=":"
              name='message' 
              id="message"
              rows={10} 
              placeholder='  ...'
              value={values.message}
              onChange={handleChange}
              onBlur={handleBlur}
            />
            {errors.message && touched.message && <p className="txtError">{errors.message}</p>}
          </label>

          <div className='w_100 right'>
            <button disabled={isSubmitting} type="submit" className='btn_green'>
                
            </button>
          </div>

        </form>
      </div>
    </motion.section>
  )
}

export default ContactCard