import { Route,Routes } from 'react-router-dom';

import Main from './pages/main/Main'
import ResearchProj from './pages/researchProj/ResearchProj'
import ImplementProj from './pages/implementProj/ImplementProj'
import Articles from './pages/articles/Articles'
import AboutMe from './pages/aboutMe/AboutMe'
import Books from './pages/books/Books'
import BooksProducts from './pages/aboutBook/AboutBook'
import ContactUs from './pages/contactUs/ContactUs'
import AcceptProject from './pages/acceptProject/AcceptProject'
import ViewPoint from './pages/viewPoint/ViewPoint'
import Media from './pages/media/Media'
import Page404 from './pages/page404/Page404'

export default function App() {
  return(
    <Routes>
      <Route path='/' element={<Main/>}/>
      <Route path='/ResearchProj' element={<ResearchProj/>}/>
      <Route path='/ImplementProj' element={<ImplementProj/>}/>
      <Route path='/Articles' element={<Articles/>}/>
      <Route path='/AboutMe' element={<AboutMe/>}/>
      <Route path='/AcceptProject' element={<AcceptProject/>}/>
      <Route path='/Books' element={<Books/>}/>
      <Route path='/BooksProducts/:name' element={<BooksProducts/>}/>
      <Route path='/ContactUs' element={<ContactUs/>}/>
      <Route path='/ViewPoint' element={<ViewPoint/>}/>
      <Route path='/Media' element={<Media/>}/>
      <Route path='*' element={<Page404 />} />
    </Routes>
  )
}
