import React, {useEffect,useState} from 'react'
import './spinner.css'

export const Spinner = ({...props}) => {
  return (
    <div className='bgSpinner center' {...props}>
       <span className="loader"></span>
    </div>
  )
}

export const SetSpinnerTime = () => {
  const [showData,setShowData] = useState(false)

  useEffect(()=>{
    setTimeout(() => {
      setShowData(true);
    }, 500);
  },[])

  return showData
}
